import { Injectable } from '@angular/core';
import {
  HttpRequest,
  HttpHandler,
  HttpEvent,
  HttpInterceptor
} from '@angular/common/http';
import { Observable } from 'rxjs';
import { JWT_NAME } from '../services/authentification-service/authentication.service';

@Injectable()
export class JwtInterceptor implements HttpInterceptor {

  constructor() {}

  intercept(request: HttpRequest<unknown>, next: HttpHandler): Observable<HttpEvent<unknown>> {
    console.log(': intercept()  intercept.ts  .');
    // console.log(': intercept()  intercept.ts.      header    .  ,  ');
    const token = localStorage.getItem(JWT_NAME);
    // console.log(': intercept()  intercept.ts.    localStorage ->', token);    
    
    if(token) {
      const cloneReq = request.clone({
        headers: request.headers.set('Authorization',
        'Bearer ' + token)
      });
      // console.log(': intercept()  intercept.ts.       headers    ->', cloneReq);
      // console.log(': intercept()  intercept.ts.  ->', next.handle(cloneReq));
      console.log(': intercept()  intercept.ts  ');
      return next.handle(cloneReq);
    } else {
      // console.log(': intercept()  intercept.ts.   ,   ->', request);
      // console.log(': intercept()  intercept.ts.  ->', next.handle(request));
      console.log(': intercept()  intercept.ts  ');
      return next.handle(request);
    }
  }
}
