import { Injectable } from '@angular/core';
import { CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot, UrlTree, Router } from '@angular/router';
import { Observable } from 'rxjs';
import { AuthenticationService } from '../services/authentification-service/authentication.service';

@Injectable({
  providedIn: 'root'
})
export class AuthGuard implements CanActivate {
  constructor(private authService: AuthenticationService, private router: Router) {}
  canActivate(): boolean {
    // console.log(':  canActivate()  authguard   ');    
    // console.log(':   canActivate  authguard. ,    .   -    .   isAuthenticated');
    
    if (!this.authService.isAuthenticated()) {
      this.router.navigate(['login']);
      // console.log(':   canActivate  authguard. authguard  false     ');
      
      return false;
    }
    // console.log(':   canActivate  authguard. authguard  true -  ');
    // console.log(':  canActivate()  authguard   ');    
    
    return true;
  }
  
}
