import { HttpClient, HttpParams } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable, Subject, throwError } from 'rxjs';
import { catchError, map, switchMap } from 'rxjs/operators';
import { User } from 'src/app/model/user.interface';
import { AuthenticationService } from '../authentification-service/authentication.service';

export interface UserData {
  docs: User[],
  totalDocs: number,
  limit: number,
  totalPages: number,
  page: number,
  pagingCounter: number,
  hasPrevPage: boolean,
  hasNextPage: boolean,
  prevPage: number
  }

@Injectable({
  providedIn: 'root'
})
export class UserService {

  constructor(private http: HttpClient,
              private authService: AuthenticationService
    ) { }

  _nickname = new Subject<User>();

  setNickname() {
    console.log('set nickname');    
    let userId = this.authService.getUserId();
    this.findOne(userId).pipe(
      map((user: User) => this._nickname.next(user))
    ).subscribe();
    
    // this._nickname.next(user);
    console.log(this._nickname);
    
  }

  // apply(o: {
  //   nickname: string
  // }) {
  //   this._nickname.next(o.nickname);
  // }


  findOne(id: string): Observable<User> {
    console.log(' - user.service: findOne()  user.service.ts   ');
    console.log(' - user.service: findOne()  user.service.ts.  id   user', id);
    console.log(' - user.service: findOne()  user.service.ts   ');
    
    return this.http.get('/api/user/' + id);
  }

  updateOne(user): Observable<User> {
    console.log(' - user.service: updateOne()  user.service.ts   ');    
    console.log(' - user.service: updateOne()  user.service.ts.  user ->', user);    
    console.log(' - user.service: updateOne()  user.service.ts.  user.id ->', user.id);    
    console.log(' - user.service: updateOne()  user.service.ts.      ->', this.http.put('api/user/' + user.id, user));    
    console.log(' - user.service: updateOne()  user.service.ts   ');        
    return this.http.put('api/user/' + user.id, user);
  }


  findAll(page: number, size: number): Observable<UserData> {
    let params = new HttpParams();

    params = params.append('page', String(page));
    params = params.append('limit', String(size));

    return this.http.get('/api/user', {params}).pipe(
      map((userData: UserData) => userData),
      catchError(err => throwError(err))
    )
  }

  uploadProfileImage(formData: FormData): Observable<any> {
    return this.http.post<FormData>('/api/user/upload', formData, {
      reportProgress: true,
      observe: 'events'
    })
  }

  paginateByName(page: number, size: number, nickname: string): Observable<UserData> {
    let params = new HttpParams();

    params = params.append('page', String(page));
    params = params.append('limit', String(size));
    params = params.append('nickname', String(nickname));

    return this.http.get('/api/user', {params}).pipe(
      map((userData: UserData) => userData),
      catchError(err => throwError(err))
    )
  }
}
